.dashboard-container {
    /* background: url('../images/fondo-login.jpg') no-repeat center center/cover; */
    background-color: #1f1f20;
    min-height: 100vh;
    padding: 0;
    margin: 0;
    color: white;
}

.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #080808;
}

.title {
    text-align: center;
    color: cyan;
    font-size: 35px;
    margin-bottom: 20px;
    margin-top: 2rem;
}

.search-bar {
    display: flex;
    width: 83%;
    align-items: center;
    background-color: transparent;
    padding-bottom: 5px;
    border-radius: 5px;
    justify-content: space-between; 
    margin-bottom: 20px;
    margin: 0 auto;
}

.search-container {
    width: 100%;
    height: 30px;
    display: flex;
    align-items:center;
    background-color: black;
    border: 2px solid cyan;
    border-radius: 5px;
    padding: 2px;
    flex-grow: 1; /* Hace que el input ocupe el espacio disponible */
}

.search-input {
    padding: 8px;
    border: none;
    outline: none;
    background: black;
    color: white;
    flex-grow: 1;
}

.clear-button-q {
    background: transparent;
    border: none;
    color: white;
    cursor: pointer;
    padding: 5px;
    width: 130px;
    height: 35px;
    margin-top: -2px;
    margin-right: -5px;
}

.search-button {
    background: cyan;
    border: none;
    padding: 8px;
    cursor: pointer;
    border-radius: 0px 5px 5px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 156px;
    height: 35px;
    margin-right: -5px;
    margin-top: -1px;
}

.search-button img {
    width: 16px;
    height: 16px;
}

.new-quote { 
    width: 30%;
    height: 50%;
    margin-left: 20px;
    padding: 10px 5px;
    border: none;
    background-color: cyan;
    color: black;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 0;
    font-family: 'Poppins', sans-serif;
}

.filtro::after {
    content: "";    
    position: fixed;
    top: 0;
    left: 0;    
    width: 100vw;
    height: 100vh;
    background: rgba(255, 189, 9, 0.144);
    pointer-events: none; /* Permite interactuar con los elementos debajo */
    z-index: 9999; /* Se asegura de que esté en la parte superior */
}

body.light-mode {
    background-color: #f6f8fa !important;
    color: #222 !important;
}

body.light-mode .dashboard-container {
    background-color: #f6f8fa !important;
    color: #222 !important;
}

body.light-mode .dashboard-header {
    background-color: #2e7dd8 !important;
}

body.light-mode .title {
    color: #00bcd4 !important;
}

body.light-mode .search-bar,
body.light-mode .search-container {
    background-color: #fff !important;
    border-color: #00bcd4 !important;
}

body.light-mode .search-input {
    background: #fff !important;
    color: #222 !important;
}

body.light-mode .clear-button-q {
    color: #222 !important;
}

body.light-mode .search-button {
    background: #00bcd4 !important;
    color: #fff !important;
}

body.light-mode .new-quote {
    background-color: #00bcd4 !important;
    color: #fff !important;
}

body.light-mode .quote-card {
    background: #fff !important;
    color: #222 !important;
    border: 1px solid #e0e0e0 !important;
}

body.light-mode .success-message {
    background: #e0f7fa !important;
    color: #00796b !important;
}

body.light-mode .filtro::after {
    background: rgba(255, 189, 9, 0.08) !important;
}

body,
.dashboard-container,
.dashboard-header,
.title,
.search-bar,
.search-container,
.search-input,
.clear-button-q,
.search-button,
.new-quote,
.quote-card,
.quote-container,
.success-message,
.filtro::after,
.quote-details,
.quote-actions,
.quotation-form,
.embla__slide,
.embla__container {
    transition: background-color 2.4s cubic-bezier(.4,2,.6,1), color 0.4s cubic-bezier(.4,2,.6,1), border-color 0.4s cubic-bezier(.4,2,.6,1), box-shadow 0.4s cubic-bezier(.4,2,.6,1);
}
